import { useState, useEffect } from "react"
import { Formlink, Wrapper, Button, Input, Buttondivs, Userdiv, Alldiv, Styledlink } from "../Styles/ListStyles";

const Eventlist = () => {

    const [ events, setEvents ] = useState([])
    const [ filtered, setFiltered ] = useState([])

    useEffect(() => {
        fetch("/api/get_events")
        .then(res => res.json())
        .then(data => {
            setEvents(data)
            setFiltered(data)
        })
        .catch(err => console.error(err))
    }, [])

    function deleteEvent(e) {
        const id = e.target.name
        fetch("/api/delete_event", {
            method: "DELETE",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({id})
        })
        events.forEach((e, index) => {
            if (e.id == id) {
                events.splice(index, 1)
                setEvents([...events])
                setFiltered([...events])
            }
        });
    }

    function Searchbar(e) {
        const search = events.filter(elem => {
            const string = elem.ime
            return string.toLowerCase().includes(e.target.value) || string.toUpperCase().includes(e.target.value)
        })
        setFiltered(search)
    }

    return (
        <Wrapper>
            <Formlink to="event_obrazac">Dodaj event +</Formlink>
            <Input onChange={Searchbar} type="search" placeholder="Pretraži..." />
            <Alldiv>
            {filtered && filtered.length ? filtered.map(event => {
                return (
                    <Userdiv key={event.id}>
                        <Styledlink to={{pathname: `event/${event.id}`}}>
                            {event.ime}
                        </Styledlink>
                        <Buttondivs>
                            <Button to={{pathname: `/event_obrazac/${event.id}`}} className="fas fa-edit"/>
                            <Button to="/eventi" onClick={deleteEvent} name={event.id} className="fas fa-trash-alt" />
                        </Buttondivs>
                    </Userdiv>
                )
            })
            :
            <Userdiv>
                Nema eventa za prikazati...
            </Userdiv>
        }
        </Alldiv>
        </Wrapper>
    )
};

export default Eventlist;
