import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
`;

const Message = styled.h1`
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4rem;
  color: black;
  padding: 2rem;
`;

const GoHome = styled.h2`
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  color: black;
  padding: 2rem;
`;

const NotFound = () => (
  <Wrapper>
    <Message>404 - Nije pronađeno!</Message>
    <GoHome>
      <Link to="/">Idi na početnu!</Link>
    </GoHome>
  </Wrapper>
);

export default NotFound;
