import { useState, useEffect, useContext } from "react"
import { UserlistContext } from "../Context/UserlContext";
import { Formlink, Wrapper, Button, Input, Buttondivs, Userdiv, Alldiv, Styledlink } from "../Styles/ListStyles";

const Userlist = () => {

    const { userlist, setUserlist } = useContext(UserlistContext);

    const [ filtered, setFiltered ] = useState()

    useEffect(() => setFiltered(userlist), [userlist])

    function deleteUser(e) {
        const id = e.target.name
        console.log(e.target)
        fetch("/api/delete_user", {
            method: "DELETE",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({id})
        })

        userlist.forEach((e, index) => {
            if (e.id == id) {
                userlist.splice(index, 1)
                setUserlist([...userlist])
            }
        });
    }

    function Searchbar(e) {
        const search = userlist.filter(elem => {
            const string = `${elem.ime} ${elem.prezime}`
            return string.toLowerCase().includes(e.target.value) || string.toUpperCase().includes(e.target.value)
        })
        setFiltered(search)
    }

    return (
        <Wrapper>
            <Formlink to="korisnik_obrazac">Dodaj člana +</Formlink>
            <Input onChange={Searchbar} type="search" placeholder="Pretraži..." />
            <Alldiv>
            {filtered && filtered.length ? filtered.map(user => {
                return (
                    <Userdiv key={user.id}>
                        <Styledlink to={{pathname: `/korisnik/${user.id}`}}>
                            {user.ime} {user.prezime}
                            <br/>
                            {user.tim}
                        </Styledlink>
                        <Buttondivs>
                            <Button to={{pathname: `/korisnik_obrazac/${user.id}`}} className="fas fa-edit"/>
                            <Button to="" onClick={deleteUser} name={user.id} className="fas fa-trash-alt" />
                        </Buttondivs>
                    </Userdiv>
                )
            })
            :
            <Userdiv>
                Nema članova za prikazati...
            </Userdiv>
        }
        </Alldiv>
        </Wrapper>
    )
};

export default Userlist;
